@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
}

.body {
    background: green url("http://localhost:8000/images/bg.svg") center/cover;
    border-radius: 1rem;
    overflow: hidden;
    -webkit-animation: 1s cubic-bezier(0.16, 1, 0.3, 1) cardEnter;
            animation: 1s cubic-bezier(0.16, 1, 0.3, 1) cardEnter;
}

.card {
    background: linear-gradient(
        to bottom right,
        rgba(255, 255, 255, 0.2),
        rgba(255, 255, 255, 0.05)
    );
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.5), -1px -1px 2px #aaa,
        1px 1px 2px #555;
    -webkit-backdrop-filter: blur(0.8rem);
            backdrop-filter: blur(0.8rem);
    padding: 1.5rem;
    border-radius: 1rem;
}

.card__row {
    display: flex;
    justify-content: space-between;
    padding-bottom: 2rem;
}

.card__title {
    font-weight: 600;
    font-size: 2.5rem;
    color: #1d005f;
    margin: 1rem 0 1.5rem;
    text-shadow: 0 2px 2px rgba(0, 0, 0, 0.3);
}

.card__col {
    padding-right: 2rem;
}

.card__input {
    background: none;
    border: none;
    border-bottom: dashed 0.2rem rgba(255, 255, 255, 0.15);
    font-size: 1.2rem;
    color: #fff;
    text-shadow: 0 2px 2px rgba(0, 0, 0, 0.3);
}
.card__input--large {
    font-size: 2rem;
}

.card__input::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 0.5);
    text-shadow: none;
}

.card__input:-ms-input-placeholder {
    color: rgba(255, 255, 255, 0.5);
    text-shadow: none;
}

.card__input::placeholder {
    color: rgba(255, 255, 255, 0.5);
    text-shadow: none;
}

.card__input:focus {
    outline: none;
    border-color: rgba(255, 255, 255, 0.6);
}

.card__label {
    display: block;
    color: #fff;
    text-shadow: 0 2px 2px rgba(0, 0, 0, 0.4);
    font-weight: 400;
}

.card__chip {
    align-self: flex-end;
}

.card__chip img {
    width: 3rem;
}

.card__brand {
    font-size: 3rem;
    color: #fff;
    min-width: 100px;
    min-height: 75px;
    text-align: right;
    text-shadow: 0 2px 2px rgba(0, 0, 0, 0.4);
}

@-webkit-keyframes cardEnter {
    from {
        -webkit-transform: translateY(100vh);
                transform: translateY(100vh);
        opacity: 0.1;
    }
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@keyframes cardEnter {
    from {
        -webkit-transform: translateY(100vh);
                transform: translateY(100vh);
        opacity: 0.1;
    }
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

